import React from 'react';

const getImagePath = (value) => {
    return "http://173.82.51.162:8094/oneall/api/products/image?id=" + value;
}

const getProduct = (props) => (
    <div className="product-card">
        <div className="product-card card">
            <img className="card-img-top" src={getImagePath(props.product.id)} alt="" width="167" height="167" />
            <div className="card-body">
                <h4 className="card-title">{props.product.name}</h4>
                <h6 className="card-subtitle mb-2 text-muted">{props.product.price}</h6>
                <p className="text-justify" style={{ fontSize: '14px' }}>
                    {props.product.description}
                </p>
                <p className="text-justify" style={{ fontSize: '14px' }}>
                    Delivery - {props.product.delivery}
                </p>
            </div>
            {/* <div className="card-footer">
                <div className="clearfix">
                    <div className="float-left mt-1">
                        <StarRating rating={props.product.rating} />
                    </div>
                    <div className="card-footer-badge float-right badge badge-primary badge-pill">{props.product.rating}</div>
                </div>
            </div> */}
        </div>
    </div>
);

const ProductCard = (props) => (
    <div>
        {getProduct(props)}
    </div>
);

export default ProductCard;


