import React, { Component } from 'react';
import 'whatwg-fetch';
import ProductList from '../../componets/ProductList';
import Loader from '../../componets/Loader'
import Intro from '../../componets/Intro'

class MostViewedProducts extends Component {

    state = {
        products: [],
        isFetching: false
    }

    componentDidMount() {

        fetch(`http://173.82.51.162:8094/oneall/api/products/mostviewed`)
            .then(response => response.json())
            .then(json => this.setState({ products: json, isFetching: false }))
            .catch(function () {
                console.log("error");
            });

    }

    render() {

        const { products, isFetching } = this.state;

        return (<div>

            <Intro message="Most Viewed Products "></Intro>

            {
                !isFetching && products.length === 0 &&
                <p>No Products </p>
            }

            {
                isFetching && <Loader />
            }
            {
                !isFetching &&

                <div className="mt-5">
                    <ProductList list={this.state.products} />
                </div>

            }

        </div>
        );
    }

}

export default MostViewedProducts;


