import React, { Component } from 'react';
import 'whatwg-fetch';
import SearchProductList from '../../componets/SearchProductList';
import Loader from '../../componets/Loader'
import MostViewedProducts from '../MostViewedProducts'
import NearMeProducts from '../NearMeProducts'

class Products extends Component {

    state = {
        products: [],
        productName: '',
        isFetching: false
    }

    onProductQueryChange = e => {

        if (e.target.value.length > 2) {
            console.log(e.target.value);
            this.setState({ productName: e.target.value, isFetching: true });
            fetch(`http://173.82.51.162:8094/oneall/api/products?query=${e.target.value}`)
                .then(response => response.json())
                .then(json => this.setState({ products: json, isFetching: false }))
                .catch(function () {
                    console.log("error");
                });;
        }

    }

    render() {

        const { products, productName, isFetching } = this.state;

        return (<div>

            <br /> <br />

            <input input={productName}
                type="text" placeholder="Search Product"
                onChange={this.onProductQueryChange}>
            </input>

            {
                !isFetching && products.length === 0 && productName.trim() !== ''
                &&
                <p>No results found for {productName} </p>
            }
            {
                isFetching && <Loader />
            }
            {
                !isFetching && <SearchProductList list={this.state.products} />
            }

            <div className="container-fluid" style={{ marginLeft: '-15px' }}>
                <div className="d-flex flex-row">
                    <div className="col-sm-12">
                        <NearMeProducts />
                    </div>
                </div>
            </div>

            <div className="container-fluid" style={{ marginLeft: '-15px' }}>
                <div className="d-flex flex-row">
                    <div className="col-sm-12">
                        <MostViewedProducts products={this.state.products} />
                    </div>
                </div>
            </div>

        </div>
        );
    }

}

export default Products;


