import React, { Component } from 'react';
import Loader from '../../componets/Loader'
import Intro from '../../componets/Intro'
import {
    Carousel,
    CarouselItem,
    CarouselControl,
    CarouselIndicators,
    CarouselCaption
} from 'reactstrap';

const getImagePath = (value) => {
    return "http://173.82.51.162:8094/oneall/api/products/image?id=" + value;
}

class ProductScroll extends Component {

    state = {
        products: [],
        activeIndex: 0,
        isFetching: false
    }

    componentDidMount() {

        fetch(`http://173.82.51.162:8094/oneall/api/products/mostviewed`)
            .then(response => response.json())
            .then(json => this.setState({ products: json, isFetching: true }))
            .catch(function () {
                console.log("error");
            });

    }

    constructor(props) {
        super(props);
        this.next = this.next.bind(this);
        this.previous = this.previous.bind(this);
        this.goToIndex = this.goToIndex.bind(this);
        this.onExiting = this.onExiting.bind(this);
        this.onExited = this.onExited.bind(this);
    }

    onExiting() {
        this.animating = false;
    }

    onExited() {
        this.animating = false;
    }

    next() {
        if (this.animating) return;
        const nextIndex = this.state.activeIndex === this.state.products.length - 1 ? 0 : this.state.activeIndex + 1;
        this.setState({ activeIndex: nextIndex });
    }

    previous() {
        if (this.animating) return;
        const nextIndex = this.state.activeIndex === 0 ? this.state.products.length - 1 : this.state.activeIndex - 1;
        this.setState({ activeIndex: nextIndex });
    }

    goToIndex(newIndex) {
        if (this.animating) return;
        this.setState({ activeIndex: newIndex });
    }

    render() {

        const { products, activeIndex, isFetching } = this.state;

        const slides = this.state.products.map((item) => {
            return (
                <CarouselItem
                    onExiting={this.onExiting}
                    onExited={this.onExited}
                    key={item.id}>
                    <img src={getImagePath(item.id)} alt={item.name} width="150" height="150" />
                    <CarouselCaption captionText={item.name} captionHeader={item.name} />
                </CarouselItem>
            );
        });

        return (<div>

            <Intro message="Most Viewed Products "></Intro>

            {
                !isFetching && products.length === 0 &&
                <p>No Products </p>
            }

            {

                <Carousel
                    activeIndex={activeIndex}
                    next={this.next}
                    previous={this.previous}>

                    <CarouselIndicators items={products} activeIndex={activeIndex} onClickHandler={this.goToIndex} />
                    {slides}
                    <CarouselControl direction="prev" directionText="Previous" onClickHandler={this.previous} />
                    <CarouselControl direction="next" directionText="Next" onClickHandler={this.next} />
                </Carousel>
            }

        </div>);
    }
}


export default ProductScroll;


