import React, { Component } from 'react';
import 'whatwg-fetch';
import ProductList from '../../componets/ProductList';
import Loader from '../../componets/Loader'
import Intro from '../../componets/Intro'
import { geolocated } from 'react-geolocated';

class NearMeProducts extends Component {

    state = {
        products: [],
        isFetching: false
    }

    componentDidMount() {

        if (this.props.isGeolocationAvailable && this.props.isGeolocationEnabled && this.props.coords != null && this.props.coords.longitude !== null && this.props.coords.latitude !== null) {
            fetch(`http://173.82.51.162:8094/oneall/api/get-recently-added-nearme?longitude=${this.props.coords.longitude}&latitude=${this.props.coords.latitude}`)
                .then(response => response.json())
                .then(json => this.setState({ products: json, isFetching: false }))
                .catch(function () {
                    console.log("error");
                });
        } else {
            this.setState({ products: [], isFetching: false })
        }
    }

    render() {


        const { products, isFetching } = this.state;

        return (<div>

            {
                !isFetching && products.length !== 0
                && this.props.isGeolocationAvailable &&
                this.props.isGeolocationEnabled &&
                <Intro message="Near Me Products "></Intro>
            }

            {
                isFetching && this.props.isGeolocationAvailable &&
                this.props.isGeolocationEnabled && <Loader />
            }

            {
                !isFetching &&
                this.props.isGeolocationAvailable &&
                this.props.isGeolocationEnabled &&

                <div className="mt-5">
                    <ProductList list={this.state.products} />
                </div>

            }

        </div>
        );
    }

}
export default geolocated({
    positionOptions: {
        enableHighAccuracy: false,
    },
    userDecisionTimeout: 5000,
})(NearMeProducts);



